body {
  display: flex;
  flex-direction: column;
  font-family: "Montserrat", sans-serif;
  background-color: rgba(0, 0, 0, 0.943);
  font-size: 1.6rem;
}
body.no-scroll {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 63px;
}

p {
  font-size: 2rem;
  color: white;
}

h1, h2, h3, h4 {
  color: white;
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

/** Media Queries **/
/* Estilos para el encabezado en estado normal */
.header {
  width: min(95%, 140rem);
  margin: 0 auto;
  background-color: black;
  width: 100%;
}
.header__titulo {
  display: none;
}
@media (min-width: 768px) {
  .header__titulo {
    display: block;
    font-family: "Montserrat", sans-serif;
    font-weight: white;
    margin: 2rem 0;
    font-size: clamp(1rem, -0.8rem + 10vw, 2rem);
    white-space: nowrap;
    text-align: center;
  }
}
.header__titulo2 {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-weight: white;
  margin: 2rem 0;
  font-size: clamp(1rem, -0.8rem + 10vw, 2rem);
  white-space: nowrap;
}
@media (min-width: 768px) {
  .header__titulo2 {
    display: none;
  }
}
.header__contenido--header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__navegacion--principal {
  display: none;
}
@media (min-width: 768px) {
  .header__navegacion--principal {
    display: flex;
    align-items: center;
    top: 0;
    right: 0;
    padding: 1rem;
  }
}
@media (min-width: 768px) {
  .header__navegacion--enlace {
    text-transform: uppercase;
    font-weight: 900;
    color: rgb(0, 255, 0);
    font-size: 0.8rem;
    padding: 1rem;
    font-size: clamp(0.8rem, -0.8rem + 10vw, 1.8rem);
  }
  .header__navegacion--enlace:hover {
    background-color: rgba(69, 67, 67, 0.178);
    color: rgb(102, 243, 102);
  }
}

.footer {
  background-color: black;
  margin-top: 5rem;
  padding-top: 3rem;
  color: white;
}
.footer__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.footer__texto {
  line-height: 2;
  text-align: center;
}
@media (min-width: 768px) {
  .footer__texto {
    text-align: left;
  }
}
.footer__contacto {
  text-align: center;
  margin: 0 0 2rem 0;
}
.footer__copyright {
  background-color: rgba(0, 0, 0, 0.943);
  margin: 0;
  padding: 2rem 0;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 900;
}
.footer__copyright--formulario {
  margin: 0 auto;
  padding: 1rem;
}
.footer__copyright--regular {
  font-weight: 400;
}

.inicio {
  background-image: url(../img/bg.png);
  background-image: image-set(url("../img/bg.avif") 1x, url("../img/bg.webp") 1x, url("../img/bg.png") 1x);
  background-size: cover;
  background-position: center;
}
.inicio__imagen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.inicio__imagen:hover {
  background-color: rgba(0, 0, 0, 0.7);
}
.inicio__imagen--overlay {
  text-align: center;
}
@media (min-width: 250px) {
  .inicio__imagen--overlay {
    width: min(95%, 140rem);
    margin: 0 auto;
    margin: 3rem;
  }
}
@media (min-width: 768px) {
  .inicio__imagen--overlay {
    width: min(95%, 80rem);
    margin: 0 auto;
    margin: 6rem;
  }
}
.inicio__imagen--overlay h2 {
  margin: auto;
}
.inicio__imagen--overlay p {
  text-align: start;
  font-size: 1.6rem;
  line-height: 2.2;
}
.inicio__imagen--overlay img {
  border-radius: 50%;
  width: 5rem;
  margin: 0 auto;
  object-fit: cover;
}
.inicio__imagen--overlay--cv {
  background-color: rgb(0, 255, 0);
  padding: 0.8rem;
  border: 3px solid rgb(102, 243, 102);
  border-radius: 0.5rem;
}
.inicio__imagen--overlay--cv a,
.inicio__imagen--overlay--cv i {
  color: black;
}

.proyecto {
  width: min(95%, 140rem);
  margin: 0 auto;
  font-size: clamp(0.5rem, -0.8rem + 10vw, 3rem);
}
.proyecto__titulo {
  width: min(95%, 80rem);
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
  font-size: clamp(1rem, -0.8rem + 10vw, 2rem);
  font-weight: 700;
  margin-bottom: 2rem;
  color: rgb(0, 255, 0);
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  border-bottom: 0.1rem solid rgb(0, 255, 0);
  margin-top: 2rem;
}
.proyecto__subtitulo {
  width: min(95%, 80rem);
  margin: 0 auto;
  padding: 2rem;
  font-size: clamp(1rem, -0.8rem + 10vw, 2rem);
  font-weight: 700;
  margin-bottom: 2rem;
  color: rgb(0, 255, 0);
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  border-bottom: 0.1rem solid rgb(0, 255, 0);
  margin-top: 2rem;
}
.proyecto ul {
  text-decoration: double;
  list-style: square;
  color: white;
  font-weight: 300;
  font-size: clamp(1rem, -0.8rem + 10vw, 2rem);
}
.proyecto__finalizado {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .proyecto__finalizado {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.proyecto__finalizado--tipo {
  border: 1px solid rgba(26, 26, 26, 0.943);
  padding: 2rem;
  width: min(95%, 80rem);
  margin: 0 auto;
}
.proyecto__finalizado--tipo--nombre {
  font-size: clamp(0.6rem, -0.8rem + 10vw, 1.5rem);
  text-align: center;
  font-weight: 900;
  margin-top: 0;
}
.proyecto__finalizado--tipo__escuela {
  font-size: clamp(0.6rem, -0.8rem + 10vw, 1.2rem);
  text-align: center;
}
.proyecto__finalizado--tipo__nombre {
  font-weight: 900;
  font-size: 2.6rem;
  margin: 2rem 0;
}
.proyecto__finalizado--tipo ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.proyecto__finalizado--tipo ul > li {
  background-color: rgb(102, 243, 102);
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1rem;
  font-weight: 700;
  color: white;
}
.proyecto__finalizado--tipo__aptitudes {
  font-size: clamp(0.6rem, -0.8rem + 10vw, 1.2rem);
  font-weight: 900;
}
.proyecto__finalizado--tipo__ver {
  font-size: clamp(0.6rem, -0.8rem + 10vw, 1.2rem);
  font-weight: 900;
  background-color: transparent;
  padding: 0.8rem;
  border: none;
}
.proyecto__finalizado--tipo__ver a, .proyecto__finalizado--tipo__ver i {
  color: white;
}
.proyecto__finalizado--tipo__descipcion {
  font-size: clamp(0.8rem, -0.8rem + 10vw, 1.3rem);
  font-weight: 300;
  display: none;
  overflow: hidden;
}
.proyecto__finalizado--tipo__descipcion--boton {
  font-size: clamp(0.6rem, -0.8rem + 10vw, 1.2rem);
  font-weight: 700;
  background-color: none;
  color: rgb(0, 255, 0);
  display: flex;
  align-items: center;
  justify-content: center;
}
.proyecto__finalizado--tipo__descipcion--boton:hover {
  cursor: pointer;
  font-size: 1.3rem;
  color: rgb(102, 243, 102);
}
.proyecto__finalizado--tipo__descipcion--boton i {
  color: white;
}
.proyecto__finalizado--tipo .completo {
  display: block;
}

